.empty_content{
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column; 
      align-items: center;
      justify-content: center;
      gap: 2rem;
      padding-top: 5rem;
}

.empty_content span *{
      color: var(--grys-100);
      font-size: 5rem;
}

.empty_content h3{
      text-align: center;
      color: var(--grys-100);
      font-size: 1.6rem;
}