.main_content{
      width: 655px;
      height: 670px;
      position: relative;
      padding: 1rem;
}

.main_content:before{
      width: 100%;
      height: 100%;
      background: rgba(255,255,255,.2);
      -webkit-backdrop-filter: blur(15px);
      -moz-backdrop-filter: blur(15px);
      backdrop-filter: blur(15px);
      top: 0;
      left: 0;
      z-index: -1;
}

.main_content:after{
      width: 500px;
      height: 670px;
      top: 30px;
      right: -30px;
      background: var(--theme-200);
      box-shadow: 0 265px var(--light) inset;
      z-index: -2;
}

.main_header{
      width: 100%;
      height: 225px;
      position: relative;
      background: var(--theme-200);
}

.main_header__banner{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
}

.banner_img{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
}

.banner_img:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: #000;
      opacity: .25;
}

.banner_img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.banner_title{
      position: relative;
      display: inline-flex;
      flex-direction: column;
      align-items: flex-start;
      color: var(--light);
      padding-bottom: 5.5rem;
}

.banner_title span{
      text-transform: uppercase;
      font-size: 2rem;
      padding: 0 1rem;
}

.banner_title h1{
      font-size: 3rem;
      font-weight: 400;
      text-align: left;
      font-family: var(--ft-4);
      padding: 0 1rem;
      line-height: 3rem;
}

.main_header__icon_menu{
      width: 30px;
      height: 30px;
      position: absolute;
      top: 2rem;
      right: 2rem;
      cursor: pointer;
}

.bar_menu{
      width: 30px;
      height: 2px;
      position: absolute;
      top: calc(50% - 1px);
      left: calc(50% - 15px);
      background: var(--light);
      transition: .3s;
}

.bar_menu:nth-child(1){
      transform: translateY(-8px);
}

.bar_menu:nth-child(3){
      transform: translateY(8px);
}

.main_header__icon_menu.active .bar_menu:nth-child(1),
.main_header__icon_menu.active .bar_menu:nth-child(2){
      transform: translateY(0) rotate(45deg);
}

.main_header__icon_menu.active .bar_menu:nth-child(3){
      transform: translateY(0) rotate(-45deg);
}

.main_header__drop_menu{
      width: 170px;
      height: 140px;
      padding: 1rem;
      background: rgba(255,255,255,.2);
      -webkit-backdrop-filter: blur(15px);
      -moz-backdrop-filter: blur(15px);
      backdrop-filter: blur(15px);
      position: absolute;
      top: 65px;
      right: 24px;
      border-radius: .6rem;
      box-shadow: 1rem 1rem 15px rgba(0,0,0,.3);
      -webkit-transform-origin: right top;
      -moz-transform-origin: right top;
      transform-origin: right top;
      opacity: 0;
      visibility: hidden;
      transform: scale(0);
}

.main_header__drop_menu.show{
      animation: show_menu .3s ease-in-out forwards;
}

@keyframes show_menu{
      0%{
            opacity: 0;
            visibility: hidden;
            transform: scale(0);
      }
      70%{
            opacity: 1;
            visibility: visible;
            transform: scale(1.4);
      }
      100%{
            opacity: 1;
            visibility: visible;
            transform: scale(1);
      }
}
.main_header__drop_menu__inner{
      width: 100%;
      height: 100%;
      display: flex;
      gap: 1rem;
      padding: 1rem;
      background: var(--theme-200);
      box-shadow: 1rem 1rem 15px rgba(0,0,0,.3), 0 0 15px rgba(0,0,0,.2) inset;
      flex-direction: column;
      border-radius: .6rem;
}

.main_header__drop_menu button{
      width: 100%;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      text-transform: uppercase;
      gap: .5rem;
      border-radius: .4rem;
      background: linear-gradient(135deg, var(--theme-200), var(--theme-400));
      font-family: var(--ft-1);
      border: solid 4px var(--theme-400);
}

.main_header__drop_menu button span{
      color: var(--light);
      font-size: 1.2rem;
      height: 32px;
      line-height: 32px;
      position: relative;
      padding-left: 1rem;
}

.main_header__drop_menu button sup{
      padding: .2rem .5rem;
      color: var(--light);
      right: -20px;
      top: -8px;
      font-size: 1.2rem;
      height: 32px;
      width: 32px;
      background: rgba(0,0,0,.1);
      border-radius: .3rem;
      line-height: 32px;
}

.main_header__social_nav{
      position: absolute;
      right: 2rem;
      bottom: 2rem;
      width: 316px;
      height: auto;
}

.social_nav__menu{
      width: 100%;
      height: 36px;
      display: flex;
      align-items: center;
      justify-content: space-between;
}

.social_nav__menu a{
      width: 36px;
      height: 36px;
      background: rgba(255,255,255,.2);
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--light);
      transition: .2s;
}

.social_nav__menu a *{
      font-size: 1.6rem;
}

.social_nav__menu a:hover{
      color: var(--theme-300);
}

.social_nav__title{
      height: 24px;
      width: 100%;
      margin-top: 1rem;
      color: var(--light);
      text-transform: uppercase;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
}

.social_nav__title:before,
.social_nav__title:after{
      width: 110px;
      height: 1px;
      background: rgba(255,255,255,.5);
      top: calc(50% - .5px);
}

.social_nav__title:before{
      left: 0;
}

.social_nav__title:after{
      right: 0;
}

.social_nav__title span{
      font-size: 1.2rem;
}

/**
* CONTAINER
*/

.container{
      width: 100%;
      height: 425px;
      background: var(--grys-100);
      box-shadow: 155px 0 var(--light) inset;
      display: flex;
      gap: 2rem;
}

.container_col{
      position: relative;
      height: 100%;
      z-index: 2;
}

.container_col:nth-child(1){
      width: 270px;
      flex-shrink: 0;
}

.wrapper_user{
      width: 230px;
      height: 410px;
      position: absolute;
      background: var(--light);
      border-radius: .6rem;
      left: 40px;
      top: -94px;
}

.wrapper_user:before{
      width: 100%;
      height: 100%;
      background: #000;
      filter: blur(25px);
      opacity: .3;
      z-index: -1;
}

.wrapper_user:after{
      width: 200px;
      height: 50%;
      background: var(--theme-300);
      border-radius: 2rem;
      left: 15px;
      bottom: -40px;
      z-index: -2;
}

.user_header{
      width: 100%;
      height: 205px;
      position: relative;
}

.user_header__banner{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: .6rem .6rem 0 0;
      background: var(--light);
      overflow: hidden;
}

.user_header__banner img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      opacity: .38;
}

.user_online{
      position: absolute;
      height: 36px;
      top: 1.5rem;
      left: 1.5rem;
      display: flex;
      gap: .5rem;
}

.user_online__icon{
      position: relative;
      width: 36px;
      height: 36px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background: var(--alpha);
      color: var(--light);
}

.user_online__icon *{
      font-size: 1.8rem;
}

.user_online__dots{
      position: absolute;
      width: 10px;
      height: 10px;
      background: var(--light);
      border-radius: 50%;
      box-shadow: 0 0 0 2px var(--alpha);
      top: 0;
      right: 0;
}

.user_online__dots.green{
      background:var(--green);
}

.user_online__dots.red{
      background: var(--red);
}

.user_online span{
      padding: 0 1.2rem;
      background: var(--alpha);
      color: var(--light);
      height: 24px;
      border-radius: 24px;
      line-height: 24px;
      text-transform: uppercase;
}

.user_main{
      position: relative;
      width: 100%;
      height: 210px;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 1rem;
}

.user_main__data{
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
}

.user_data__avatar{
      width: 90px;
      height: 90px;
      border-radius: 50%;
      position: absolute;
      top: -45px;
      left: calc(50% - 45px);
      background: var(--grys-300);
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: 0 0 0 5px var(--theme-200);
}

.user_data__info{
      margin-top: 45px;
      min-width: 70%;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
}

.user_data__info h3{
      color: var(--theme-200);
      font-size: 1.6rem;
}

.user_data__info h2{
      color: var(--dark);
      font-size: 2rem;
      font-family: var(--ft-1);
      line-height: 2rem;
}

.user_data__info p{
      font-size: 1.2rem;
      color: var(--grys-300);
}

.user_main__relation{
      width: 190px;
      height: 62px;
      border-radius: 62px;
      background: rgba(0,0,0,.03);
      padding: .5rem;
      display: flex;
      gap: 1rem;
      align-items: center;
      margin-top: 1.5rem;
}

.relation_type{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      gap: .5rem;
      position: relative;
}

.relation_type span{
      height: 26px;
      line-height: 26px;
      padding: 0 1.5rem;
      border-radius: 26px;
      background: var(--dark);
      color: var(--light);
      text-transform: uppercase;
      font-size: 1.2rem;
      width: auto;
      min-width: 60px;
      display: inline-flex;
      align-items: center;
      position: absolute;
      top: -13px;
      left: -1rem;
}

.relation_avatar{
      width: 48px;
      height: 48px;
      background: var(--grys-200);
      border-radius: 50%;
      margin: 2px;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      flex-shrink: 0;
      box-shadow: 0 0 0 2px var(--alpha);
}

.relation_avatar img{
      width: 48px;
      height: 48px;
      border-radius: 50%;
}

.relation_data{
      flex-grow: 1;
      display:  flex;
      flex-direction: column;
      align-items: flex-end;
}

.relation_data h2{
      color: var(--dark);
      text-transform: uppercase;
      font-size: 1.3rem;
}

.relation_data a{
      background: var(--theme-200);
      color: var(--light);
      text-transform: uppercase;
      padding: .3rem 1rem;
      border-radius: 999px;
}

.user_main__location{
      width: 200px;
      height: 40px;
      padding: 1rem;
      display: flex;
      align-items: flex-end;
      position: absolute;
      top: calc(100% - .5rem);
      left: 15px;
}

.user_main__location *{
      color: var(--light);
      font-size: 2rem;
}

.user_main__location span{
      font-size: 1.2rem;
      text-transform: uppercase;
}

.container_col:nth-child(2){
      padding: 2rem;
      padding-left: 0;
}

.user_about{
      width: 100%;
      height: auto;
      max-height: 150px;
      padding-bottom: 1rem;
      border-bottom: solid 1px rgba(0,0,0,.05);
      overflow: hidden;
}

.user_about p{
      font-size: 1.2rem;
      color: var(--grys-300);
}

.wrapper_video{
      margin-top: 2rem;
}

.video_box{
      width: 100%;
      height: 191px;
      background: var(--dark);
      border-bottom: solid .5rem var(--theme-200);
      position: relative;
      box-shadow: 1.5rem 1.5rem 25px rgba(0,0,0,.3);
}

.video_box iframe{
      border: none;
}

.footer{
      width: 510px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 100%;
      right: 0;
}

.footer p{
      font-size: 1.2rem;
}