.friend{
      position: absolute;
      top: 1rem;
      right: 1rem;
      width: 295px;
      height: 650px;
      z-index: 50;
      overflow: hidden;
      visibility: hidden;
}

.friend.show{
      visibility: visible;
}

.friend_content{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: rgba(255,255,255,.2);
      -webkit-backdrop-filter: blur(15px);
      -moz-backdrop-filter: blur(15px);
      backdrop-filter: blur(15px);
      transform: translateX(100%);
}

.friend.show .friend_content{
      animation: show_friend .3s ease-in-out forwards;
}

@keyframes show_friend{
      100%{
            transform: translateX(0);
      }
}

.friend_group{
      width: 100%;
      height: 610px;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--light) var(--theme-200);
}

.friend_col{
      width: calc(100% - .5rem);
      height: auto;
}

.friend_card{
      width: 100%;
      height: 80px;
      background: var(--light);
      color: var(--grys-300);
      display: flex;
      gap: 1rem;
      margin-bottom: .2rem;
      align-items: center;
}

.friend_card:hover{
      background: var(--dark);
}

.friend_card img{
      width: 80px;
      height: 80px;
      flex-shrink: 0;
}

.friend_data{
      height: 100%;
      flex-grow: 1;
      position: relative;
      padding: 1rem;
      padding-left: 0;
}

.friend_data h3{
      font-size: 1.6rem;
}

.friend_card:hover h3{
      color: var(--light);
}

.friend_data p{
      font-size: 1.2rem;
      margin-top: .5rem;
}

.friend_data a{
      position: absolute;
      width: 60px;
      height: 21px;
      top: .5rem;
      right: 1rem;
      background: var(--theme-200);
      color: var(--light);
      text-transform: uppercase;
      text-align: center;
      line-height: 21px;
      font-size: 1.1rem;
}

.close_friend{
      width: 100%;
      height: 40px;
      text-transform: uppercase;
      background: var(--dark);
      color: var(--grys-300);
      font-size: 2rem;
}