@import url('https://fonts.googleapis.com/css2?family=Courier+Prime:wght@400;700&family=Fira+Mono:wght@400;500;700&family=Poppins:wght@200;300;400;500;600&family=Xanh+Mono&display=swap');

:root{
  /** fonts */
  --ft-1: 'Poppins', sans-serif;
  --ft-2: 'Courier Prime', monospace;
  --ft-3: 'Fira Mono', monospace;
  --ft-4: 'Xanh Mono', monospace;

  /** colors */
  --green: #45d985;
  --dark: #080908;
  --light: #fff;
  --red: #d90000;
  --grys-300: #8a8a8a;
  --grys-200: #b4b2b2;
  --grys-100: #fcfaf8;
  --alpha: rgba(0,0,0,.38);

  /** default theme */
  --theme-400: #e84f3b;
  --theme-300: #ff6958;
  --theme-200: #fd817c;
  --theme-100: #ffe5e3;

  /** globals */
  font-family: var(--ft-2);
  font-size: 62.5%;

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  min-height: 920px;
  overflow-x: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: radial-gradient(var(--theme-200), var(--theme-400));
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

::-webkit-scrollbar{
  width: .8rem;
  background: var(--light);
}

::-webkit-scrollbar-thumb{
  background: var(--theme-200);
}